.password-prompt {
    background-image: url(https://www.accelerance.com/hubfs/password-page/bg.svg);
    background-size: 100% auto;
    background-color: $blue;
    color: $white;
    .header-container-wrapper {
        background-image: none;
    }
    .main {
        height: 70vh;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        text-align: center;
        padding: 40px 0px;
        & * {
            width: 100%;
            flex: 0 0 100%; /* fg: 0, fs: 0, fb: 100% */
        }
        .logo-container {
            img {
                width: initial;
            }
        }
        .error-msg {
            color: red;
        }
        input {
            border:none;
            background-image:none;
            background-color: transparent;
            -webkit-box-shadow: none;
            -moz-box-shadow: none;
            box-shadow: none;
            font-family: $font-stack;
            &[type=password] {
                background-color: rgba(255, 255, 255, 0.95);
                padding: 10px 15px;
                width: 85%;
                font-size: 18px;
                border-radius: 30px;
                margin: 36px auto!important;
            }
            &[type=submit] {
                color: white;
                padding: 15px 30px;
                font-size: 18px;
                font-weight: 600;
                background-color: $cyan;
                border-radius: 30px;
                width: 50%;
                -webkit-appearance: none;
                cursor: pointer;
                @include transition(0.2s);
                &:hover {
                    background-color: $cyan-hover;
                }
            }
            &:focus {
                outline: 0;
            }
        }
        @media screen and (max-width: $tablet) {
            padding: 10px;
            .logo-container {
                img {
                    width: 300px;
                }
            }
            input[type=submit] {
                width: 70%;
            }
        }
    }
}